body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.loader {
	z-index: 100;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100px;
    height: 100px;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    opacity: 0;
    /* visibility: hidden; */
    -webkit-transition-delay: 0.2s;
         -o-transition-delay: 0.2s;
            transition-delay: 0.2s;
    .icon {
        width: 100px;
        height: 100px;
        position: absolute;
        left: 0;
        top: 0;
        background: url('./Images/ico_loader.svg') no-repeat 0 0;
        background-size: 248px 248px;
    }
}
.is-loading {
    .loader {
        opacity: 1;
        visibility: visible;
        -webkit-transition: opacity 0.3s linear, visibility 0.3s linear;
        -o-transition: opacity 0.3s linear, visibility 0.3s linear;
        transition: opacity 0.3s linear, visibility 0.3s linear;
        z-index: 3;
        .icon {
            -webkit-animation: spin 500ms infinite linear;
                    animation: spin 500ms infinite linear;
        }
    }
}

@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0); transform: rotate(0); -webkit-transform-origin: center center; transform-origin: center center; }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); -webkit-transform-origin: center center; transform-origin: center center; }
}

@keyframes spin {
    0% { -webkit-transform: rotate(0); transform: rotate(0); -webkit-transform-origin: center center; transform-origin: center center; }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); -webkit-transform-origin: center center; transform-origin: center center; }
}


